import React from 'react'

const movie = ({img, author, title}) => {
    const clickHandler = () => {
        alert('Welcome to Otaku World')
      }
    return (
     <div>
        <section className = 'book'>
            <img src={img} alt=""/>
            <h2>{title}</h2>
            <h4>{author}</h4>
            <button type = 'button' onClick = {clickHandler}>Cick me</button>
        </section>
     </div>
    )
}

export default movie
